.project-post {
  max-width: $width-limit;
}

@media screen and (max-width: $width-limit) {
  .project-post  {
    max-width: 100%;
  }

  .project-post img, .project-post video {
    max-width: 95% !important;
    display: block;
    margin: 6px auto !important;
  }

  @media screen and (max-width: $width-limit) {
    .project-post img, .project-post video {
      width: 95%;
    }
  }
}

#projectsList {
  max-width: 550px;
  margin-top: 0;
}

#projectsList li h1 {
  margin-bottom: 10px;
}

#projectsList p {
  margin-top: 0;
}

#projectsList li::before {
  display: none;
}

.project-header {
  display: flex;
  align-items: baseline;
}

@media screen and (max-width: 500px) {
  .project-header {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  .project-header .date {
    margin: 5px 0 !important;
  }
}

.project-header .date {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.project-title {
  font-size: 40px;
  color: black;
}

#projectHeaderBar {
  border: 1px solid $selected-color;
}

.project-post img {
  border: 3px solid $selected-color;
  max-width: $width-limit;
  margin: 3px;
}

.project-post video {
  border: 3px solid $selected-color;
  max-width: $width-limit;
  margin: 3px;
}